import React, { useState } from 'react';
import Button from '@material-ui/core/Button';

import { useHistory } from 'react-router-dom';

const Start = () => {
    const history = useHistory();

    const handleClick = (event: any) => {
        history.push('/home');
    }

    return(
        <div>
            <p id="startStyle">
              Welcome to Luke and Jake's Website!
           </p>
            <img className="imageAdjuster" id="borderColorGreen" src={require('../images/startingimage1.JPG').default} height={490} width={653}/>
            <br/>
            <Button variant="contained" color="secondary" size="large" onClick={handleClick}> 
                Continue to website!
            </Button>
        </div>
    )
}

export default Start;