import React from 'react';
import Button from '@material-ui/core/Button';
import { useHistory } from 'react-router-dom';

import { View } from "react-view";
import { StylesProvider, ThemeProvider } from '@material-ui/styles';
import { createMuiTheme, Typography } from '@material-ui/core';

const JakeProjectPage = () => {
    const history = useHistory();

    const backJakePage = (event: any) => {
        history.goBack();
    }

    const backHomePage = (event: any) => {
        history.goBack();
        history.goBack();
        history.goBack();
    }

    const theme = createMuiTheme({
        typography:{
            fontSize:25,
        },
    });

    return(
        <div>
            <div>
                <h1 id="aboutProjectsHeader">
                    Projects:
                </h1>
                <div>
                    <ThemeProvider theme={theme}>
                        <Typography align="left"> 
                        <div>
                            <a href="https://github.com/Jay-Beezy/Gambling-Craps-Table" id="skillsHeader" target="_blank">Gambling Craps Table</a>
                            <li> 
                                Built a fully functional craps table (like one you would find at a casino).
                            </li> 
                            <li>
                                The user can make a variety of bets.
                            </li>
                            <li>
                                Based on the roll of the dice and their current bets, the user can win / lose "money"
                            </li>
                            <li>
                                Runs through the command line.
                            </li>
                            <li>
                                Purpose: For fun!
                            </li>
                            <li>
                                Programming Language: C++
                            </li>
                        </div>
                        <div>
                            <br/>
                            <a href="https://github.com/Jay-Beezy/Connect-Four" id="skillsHeader" target="_blank">Connect Four</a>
                            <li> 
                                Built a standard version of a two-player Connect Four game.
                            </li>
                            <li>
                                Utilizes event handlers and rendering functions to display the accurate chips.
                            </li>
                            <li>
                                Purpose: Programming III (EECS 368)
                            </li>
                            <li>
                                Programming Language: JavaScript, HTML
                            </li>
                        </div>
                        <div>
                            <br/>
                            <a id="skillsHeader">
                                This Website!
                            </a>
                            <li> 
                                Using React, custom built a website to demonstrate skills in programming and personal information.
                            </li> 
                            <li>
                                Utilized resources during KU's Hack-A-Thon to create this website!
                            </li>
                            <li>
                                First time using React and typescript, created by Jake and Luke as a team!
                            </li>
                            <li>
                                Purpose: University of Kansas "HackKU 2021" Event!
                            </li>
                            <li>
                                Programming Language: Typescript, HTML, CSS
                            </li>
                        </div>
                        </Typography>
                    </ThemeProvider>
                    <h5 id="aboutProjectsHeader">
                        Future Projects (Currently in development):
                    </h5>
                    <ThemeProvider theme={theme}>
                        <Typography align="left"> 
                        <div>
                            <a href="https://github.com/Jay-Beezy/Craps_JavaScript" id="skillsHeader" target="_blank">Gambling Craps Table 2.0</a>
                            <li> 
                                Similar in nature to the craps table implemented in C++
                            </li> 
                            <li>
                                Instead of running through a command line, the user is visually displayed the craps table.
                            </li>
                            <li>
                                The user can click on certain parts of the table to place bets.
                            </li>
                            <li>
                                Hope to implement sliders in order for the user to make accurate and acceptable bets.
                            </li>
                            <li>
                                Purpose: Gain a better understanding of JavaScript, and to better visualize the premise of craps!
                            </li>
                            <li>
                                Programming Language: JavaScript, HTML, CSS
                            </li>
                        </div>
                        </Typography>
                    </ThemeProvider>

                </div>
            </div>
            <br/>
            <Button variant="contained" color="secondary" id="buttonRight" onClick={backJakePage}> 
                Back
            </Button>
            <Button variant="contained" color="secondary" id="buttonRight" onClick={backHomePage}> 
                Home
            </Button>
            <p>
            </p>
        </div>
    )
}

export default JakeProjectPage;