import React, { useState } from 'react';
import Button from '@material-ui/core/Button';
import { useHistory } from 'react-router-dom';
import { createMuiTheme, Typography } from '@material-ui/core';
import { StylesProvider, ThemeProvider } from '@material-ui/styles';

const JakeMoviePage = () => {
    const history = useHistory();

    const backJakePage = (event: any) => {
        history.goBack();
    }

    const backHomePage = (event: any) => {
        history.goBack();
        history.goBack();
        history.goBack();
    }

    const theme = createMuiTheme({
        typography:{
            fontSize:25,
        },
    });

    const [showMovie1, setShow1]=useState(false)
    const [showMovie2, setShow2]=useState(false)
    const [showMovie3, setShow3]=useState(false)
    const [showMovie4, setShow4]=useState(false)
    const [showMovie5, setShow5]=useState(false)
    const [showMovie6, setShow6]=useState(false)
    const [showMovie7, setShow7]=useState(false)
    const [showMovie8, setShow8]=useState(false)
    const [showMovie9, setShow9]=useState(false)
    const [showMovie10, setShow10]=useState(false)
    const [showMovie11, setShow11]=useState(false)

    return(
        <div>
            <div>
                <h1 id="aboutHeader">
                    Jake's Favorite Movies (I really LOVE movies!):
                </h1>
                <p>
                    In no particular order..
                </p>
                <div>
                <ThemeProvider theme={theme}>
                    <Typography align="left"> 
                        <div id="filmStyle">
                            <li>
                                <button className="movieButton" onClick={()=>setShow1(!showMovie1)}><b>Hot Rod</b></button>
                                <i>  (Dir. Akiva Schaffer, Stars: Andy Samberg, Isla Fisher, Bill Hader)</i>
                            </li>
                            {showMovie1?<iframe height="800vh" width="100%" src="https://www.youtube.com/embed/yByhd7FAOug"></iframe>:null }
                            <li>
                                <button className="movieButton" onClick={()=>setShow2(!showMovie2)}><b>12 Monkeys</b></button>
                                <i>  (Dir. Terry Gilliam, Stars: Bruce Willis, Brad Pitt, Madeleine Stowe)</i>
                            </li>
                            {showMovie2?<iframe height="800vh" width="100%" src="https://www.youtube.com/embed/15s4Y9ffW_o"></iframe>:null }
                            <li>
                                <button className="movieButton" onClick={()=>setShow3(!showMovie3)}><b>Napoleon Dynamite</b></button>
                                <i>  (Dir. Jared Hess, Stars: Jon Heder, Efren Ramirez, Tina Majorino)</i>
                            </li>
                            {showMovie3?<iframe height="800vh" width="100%" src="https://www.youtube.com/embed/EJP3eOJ-E-I"></iframe>:null }
                            <li>
                                <button className="movieButton" onClick={()=>setShow4(!showMovie4)}><b>Fight Club</b></button>
                                <i>  (Dir. David Fincher, Stars: Edward Norton, Brad Pitt, Helena Bonham Carter)</i>
                            </li>
                            {showMovie4?<iframe height="800vh" width="100%" src="https://www.youtube.com/embed/qtRKdVHc-cE"></iframe>:null }
                            <li>
                                <button className="movieButton" onClick={()=>setShow5(!showMovie5)}><b>The Godfather</b></button>
                                <i>  (Dir. Francis Ford Coppola, Stars: Marlon Brando, Al Pacino, Diane Keaton)</i>
                            </li>
                            {showMovie5?<iframe height="800vh" width="100%" src="https://www.youtube.com/embed/sY1S34973zA"></iframe>:null }
                            <li>
                                <button className="movieButton" onClick={()=>setShow6(!showMovie6)}><b>Pulp Fiction</b></button>
                                <i>  (Dir. Quentin Tarantino, Stars: John Travolta, Samuel L. Jackson, Uma Thurman)</i>
                            </li>
                            {showMovie6?<iframe height="800vh" width="100%" src="https://www.youtube.com/embed/s7EdQ4FqbhY"></iframe>:null }
                            <li>
                                <button className="movieButton" onClick={()=>setShow7(!showMovie7)}><b>The Big Lebowski</b></button>
                                <i>  (Dir. The Coen Brothers, Stars: Jeff Bridges, John Goodman, Julianne Moore)</i>
                            </li>
                            {showMovie7?<iframe height="800vh" width="100%" src="https://www.youtube.com/embed/cd-go0oBF4Y"></iframe>:null }
                            <li>
                                <button className="movieButton" onClick={()=>setShow8(!showMovie8)}><b>Butch Cassidy and the Sundance Kid</b></button>
                                <i>  (Dir. George Roy Hill, Stars: Paul Newman, Robert Redford)</i>
                            </li>
                            {showMovie8?<iframe height="800vh" width="100%" src="https://www.youtube.com/embed/YdJW2UxvSFQ"></iframe>:null }
                            <li>
                                <button className="movieButton" onClick={()=>setShow9(!showMovie9)}><b>Interstellar</b></button>
                                <i>  (Dir. Christopher Nolan, Stars: Matthew McConaughey, Anne Hathaway, Jessica Chastain)</i>
                            </li>
                            {showMovie9?<iframe height="800vh" width="100%" src="https://www.youtube.com/embed/2LqzF5WauAw"></iframe>:null }
                            <li>
                                <button className="movieButton" onClick={()=>setShow10(!showMovie10)}><b>The Place Beyond the Pines</b></button>
                                <i>  (Dir. Derek Cianfrance, Stars: Ryan Gosling, Bradley Cooper, Eva Mendes)</i>
                            </li>
                            {showMovie10?<iframe height="800vh" width="100%" src="https://www.youtube.com/embed/KepnbJT5nBw"></iframe>:null }
                            <li>
                                <button className="movieButton" onClick={()=>setShow11(!showMovie11)}><b>The Life Aquatic with Steve Zissou</b></button>
                                <i>  (Dir. Wes Anderson, Stars: Bill Murray, Owen Wilson, Cate Blanchett)</i>
                            </li>
                            {showMovie11?<iframe height="800vh" width="100%" src="https://www.youtube.com/embed/yh401Rmkq0o"></iframe>:null }
                        </div>
                    </Typography>
                </ThemeProvider>  
                </div>
            </div>
            <br/>
            <Button variant="contained" color="secondary" id="buttonRight" onClick={backJakePage}> 
                Back
            </Button>
            <Button variant="contained" color="secondary" id="buttonRight" onClick={backHomePage}> 
                Home
            </Button>
        </div>
    )
}

export default JakeMoviePage;