import React from 'react';
import Button from '@material-ui/core/Button';
import { useHistory } from 'react-router-dom';

import { useState, useEffect } from 'react';

const JakeImagePage = () => {
    const history = useHistory();

    const backJakePage = (event: any) => {
        history.goBack();
    }

    return(
        <div>
            <div>
                <br/>
                <img className="imageAdjuster" id="borderColorGreen" src={require('../images/jakeimage3.JPG').default}/>
                <img className="imageAdjuster" id="borderColorPink" src={require('../images/jakeimage8.JPG').default}/>
                <img className="imageAdjuster" id="borderColorOrange" src={require('../images/jakeimage7.JPG').default}/>
                <img className="imageAdjuster" id="borderColorBlue" src={require('../images/jakeimage11.JPG').default}/>
                <img className="imageAdjuster" id="borderColorGreen" src={require('../images/jakeimage12.JPG').default}/>
                <img className="imageAdjuster" id="borderColorPink" src={require('../images/jakeimage4.JPG').default}/>
                <img className="imageAdjuster" id="borderColorOrange" src={require('../images/jakeimage5.JPG').default}/>
                <img className="imageAdjuster" id="borderColorBlue" src={require('../images/jakeimage6.JPG').default}/>
                <img className="imageAdjuster" id="borderColorGreen" src={require('../images/jakeimage13.jpg').default}/>
                <br/>
                <img className="imageAdjuster2" id="borderColorPink" src={require('../images/jakeimage2.JPG').default}/>
                <br/>
                <img className="imageAdjuster2" id="borderColorBlue" src={require('../images/jakeimage10.JPG').default}/>
                <br/>
                <img className="imageAdjuster2" id="borderColorOrange" src={require('../images/jakeimage14.JPG').default}/>
            </div>
            <br/>
            <Button variant="contained" color="secondary" id="buttonRight" onClick={backJakePage}> 
                Back
            </Button>
            <br/>
            <br/>
            <br/>
        </div>
    )
}

export default JakeImagePage;